import { useState, useEffect } from 'react'
import { supabase_admin } from '@/lib/supabase_admin'
import AdminLayout from '@/components/AdminLayout'
import { ChevronLeft, ChevronRight, User, Phone, MapPin, Clock } from 'lucide-react'
import { useRouter } from 'next/router'

const COLUMNS = [
  { id: 'OFFEN', title: 'Offen', color: 'bg-gray-100', icon: 'üîî' },
  { id: 'IN_BEARBEITUNG', title: 'In Bearbeitung', color: 'bg-blue-100', icon: 'üë®‚Äçüç≥' },
  { id: 'AN_FAHRER', title: 'An Fahrer', color: 'bg-orange-100', icon: 'üöó' },
  { id: 'GELIEFERT', title: 'Geliefert', color: 'bg-green-100', icon: '‚úÖ' }
]

function OrderCard({ order, columnIndex, onMoveLeft, onMoveRight, onAssignDriver, drivers, onClick }) {
  const canMoveLeft = columnIndex > 0
  const canMoveRight = columnIndex < COLUMNS.length - 1
  const status = COLUMNS[columnIndex].id

  return (
    <div className="bg-white rounded-lg p-4 shadow-sm border-2 border-gray-200 hover:shadow-md transition mb-3">
      
      {/* Arrow Buttons */}
      <div className="flex items-center justify-between mb-3">
        <button
          onClick={(e) => {
            e.stopPropagation()
            onMoveLeft()
          }}
          disabled={!canMoveLeft}
          className={`p-2 rounded-lg transition ${
            canMoveLeft 
              ? 'hover:bg-gray-200 text-gray-700' 
              : 'text-gray-300 cursor-not-allowed'
          }`}
          title={canMoveLeft ? `‚Üê ${COLUMNS[columnIndex - 1].title}` : ''}
        >
          <ChevronLeft size={20} />
        </button>

        <span className="font-bold text-sm">
          #{order.order_number || order.id.substring(0, 8).toUpperCase()}
        </span>

        <button
          onClick={(e) => {
            e.stopPropagation()
            onMoveRight()
          }}
          disabled={!canMoveRight}
          className={`p-2 rounded-lg transition ${
            canMoveRight 
              ? 'hover:bg-gray-200 text-gray-700' 
              : 'text-gray-300 cursor-not-allowed'
          }`}
          title={canMoveRight ? `${COLUMNS[columnIndex + 1].title} ‚Üí` : ''}
        >
          <ChevronRight size={20} />
        </button>
      </div>

      {/* Price */}
      <div className="text-center mb-3">
        <span className="font-bold text-2xl">{order.total.toFixed(2)} ‚Ç¨</span>
      </div>

      {/* Customer Info */}
      <div 
        className="space-y-2 text-xs mb-3 cursor-pointer"
        onClick={onClick}
      >
        <div className="flex items-center gap-2">
          <User size={14} className="text-gray-400" />
          <span className="font-semibold">{order.customer_name}</span>
        </div>
        <div className="flex items-center gap-2">
          <Phone size={14} className="text-gray-400" />
          <span>{order.customer_phone}</span>
        </div>
        <div className="flex items-center gap-2">
          <MapPin size={14} className="text-gray-400" />
          <span className="truncate">{order.delivery_address}</span>
        </div>
        <div className="flex items-center gap-2">
          <Clock size={14} className="text-gray-400" />
          <span>{new Date(order.created_at).toLocaleTimeString('de-DE', { hour: '2-digit', minute: '2-digit' })}</span>
        </div>
      </div>

      {/* Items */}
      <div className="text-xs text-gray-600 mb-3 border-t pt-2">
        {order.items.slice(0, 2).map((item, idx) => (
          <div key={idx}>
            {item.quantity}x {item.name}
          </div>
        ))}
        {order.items.length > 2 && (
          <div className="text-gray-400">
            +{order.items.length - 2} weitere
          </div>
        )}
      </div>

      {/* Driver Assignment */}
      {status === 'AN_FAHRER' && (
        <div className="border-t pt-2" onClick={(e) => e.stopPropagation()}>
          {order.driver_id ? (
            <div className="text-xs bg-blue-50 rounded px-2 py-1">
              üöó {drivers.find(d => d.id === order.driver_id)?.name || 'Fahrer zugewiesen'}
            </div>
          ) : (
            <select
              onChange={(e) => onAssignDriver(order.id, e.target.value)}
              className="text-xs w-full border rounded px-2 py-1"
              onClick={(e) => e.stopPropagation()}
            >
              <option value="">Fahrer zuweisen...</option>
              {drivers.map(driver => (
                <option key={driver.id} value={driver.id}>
                  {driver.name}
                </option>
              ))}
            </select>
          )}
        </div>
      )}

      {/* Delivered Info */}
      {status === 'GELIEFERT' && order.delivered_at && (
        <div className="text-xs text-green-600 border-t pt-2">
          ‚úÖ {new Date(order.delivered_at).toLocaleString('de-DE')}
        </div>
      )}
    </div>
  )
}

export default function KanbanPage() {
  const router = useRouter()
  const [orders, setOrders] = useState({
    OFFEN: [],
    IN_BEARBEITUNG: [],
    AN_FAHRER: [],
    GELIEFERT: []
  })
  const [loading, setLoading] = useState(true)
  const [drivers, setDrivers] = useState([])

  useEffect(() => {
    loadOrders()
    loadDrivers()
    
    const interval = setInterval(loadOrders, 30000)
    return () => clearInterval(interval)
  }, [])

  const loadOrders = async () => {
    const { data } = await supabase_admin
      .from('orders')
      .select('*')
      .order('created_at', { ascending: false })

    if (data) {
      const grouped = {
        OFFEN: [],
        IN_BEARBEITUNG: [],
        AN_FAHRER: [],
        GELIEFERT: []
      }

      data.forEach(order => {
        const status = order.status || 'OFFEN'
        if (grouped[status]) {
          grouped[status].push(order)
        }
      })

      setOrders(grouped)
    }
    setLoading(false)
  }

  const loadDrivers = async () => {
    const { data } = await supabase_admin
      .from('drivers')
      .select('*')
      .eq('is_active', true)
      .order('name', { ascending: true })

    if (data) setDrivers(data)
  }

  const moveOrder = async (orderId, currentColumnIndex, direction) => {
    const newColumnIndex = currentColumnIndex + direction
    if (newColumnIndex < 0 || newColumnIndex >= COLUMNS.length) return

    const currentStatus = COLUMNS[currentColumnIndex].id
    const newStatus = COLUMNS[newColumnIndex].id

    console.log('Moving order:', orderId)
    console.log('From:', currentStatus, '(index', currentColumnIndex, ')')
    console.log('To:', newStatus, '(index', newColumnIndex, ')')

    // Update in database
    const updateData = {
      status: newStatus,
      updated_at: new Date().toISOString()
    }

    if (newStatus === 'GELIEFERT') {
      updateData.delivered_at = new Date().toISOString()
      console.log('Setting delivered_at:', updateData.delivered_at)
    }

    if (newStatus === 'AN_FAHRER') {
      const order = orders[currentStatus].find(o => o.id === orderId)
      if (!order.assigned_at) {
        updateData.assigned_at = new Date().toISOString()
      }
    }

    console.log('Update data:', updateData)

    const { data, error } = await supabase_admin
      .from('orders')
      .update(updateData)
      .eq('id', orderId)
      .select()

    if (error) {
      console.error('Update error:', error)
    } else {
      console.log('Update successful:', data)
      loadOrders()
    }
  }

  const assignDriver = async (orderId, driverId) => {
    const { error } = await supabase_admin
      .from('orders')
      .update({ 
        driver_id: driverId,
        status: 'AN_FAHRER',
        assigned_at: new Date().toISOString()
      })
      .eq('id', orderId)

    if (!error) {
      loadOrders()
    }
  }

  if (loading) {
    return (
      <AdminLayout>
        <div className="p-8">L√§dt...</div>
      </AdminLayout>
    )
  }

  return (
    <AdminLayout>
      <div className="p-8">
        <div className="mb-6">
          <h1 className="text-3xl font-display font-bold italic mb-2">Bestellungen Kanban</h1>
          <p className="text-gray-600">Verwende die Pfeile ‚Üê ‚Üí um Bestellungen zu verschieben</p>
        </div>

        <div className="grid grid-cols-4 gap-4">
          {COLUMNS.map((column, columnIndex) => (
            <div key={column.id} className={`rounded-lg ${column.color} p-4`}>
              
              {/* Column Header */}
              <div className="mb-4">
                <h2 className="font-bold text-lg flex items-center gap-2">
                  <span>{column.icon}</span>
                  <span>{column.title}</span>
                  <span className="text-sm font-normal text-gray-600">
                    ({orders[column.id].length})
                  </span>
                </h2>
              </div>

              {/* Orders */}
              <div className="min-h-[500px]">
                {orders[column.id].map((order) => (
                  <OrderCard
                    key={order.id}
                    order={order}
                    columnIndex={columnIndex}
                    onMoveLeft={() => moveOrder(order.id, columnIndex, -1)}
                    onMoveRight={() => moveOrder(order.id, columnIndex, 1)}
                    onAssignDriver={assignDriver}
                    drivers={drivers}
                    onClick={() => router.push(`/admin/orders/${order.id}`)}
                  />
                ))}
              </div>

            </div>
          ))}
        </div>

      </div>
    </AdminLayout>
  )
}